<template>
<div>
  <el-form :inline="true"  class="high-form-inline" :model="form1" label-width="100px" v-if="sekey==1">
        <el-form-item label=":">
           <el-select v-model="form1.year" placeholder="">
            <el-option
              v-for="item in years"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model="form1.region" placeholder="">
            <el-option
              v-for="item in divisions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
         <el-form-item label=":">
          <el-select v-model="form1.type" placeholder="">
          <el-option
              v-for="item in types"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
         <el-form-item label=":">
          <el-select v-model="form1.xtype" placeholder="">
            <el-option
              v-for="item in xztypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form1.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
           <el-date-picker
              v-model="form1.time"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder="">
            </el-date-picker>
        </el-form-item>
          <div class="subbtns">
            <el-button type="primary" @click="search"></el-button>
            <el-button></el-button>
          </div>
      </el-form>

      <el-form :inline="true"  class="high-form-inline" :model="form2" label-width="130px" v-if="sekey!=1">
        <el-form-item label=":">
           <el-select v-model="form2.year" placeholder="">
            <el-option
              v-for="item in years"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model="form2.region" placeholder="">
            <el-option
              v-for="item in divisions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
         <el-form-item label=":">
          <el-select v-model="form2.type" placeholder="">
             <el-option
              v-for="item in types"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
         <el-form-item label=":">
          <el-select v-model="form2.xtype" placeholder="">
            <el-option
              v-for="item in xztypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        
        <el-form-item label=":">
          <el-input v-model="form2.name" placeholder=""></el-input>
        </el-form-item>
         <el-form-item label=":">
          <el-select v-model="form2.fgtype" placeholder="">
            <el-option
              v-for="item in fgtypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model="form2.jstype" placeholder="">
            <el-option
              v-for="item in jstypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model="form2.pytype" placeholder="">
            <el-option
              v-for="item in pytypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
           <el-date-picker
              v-model="form2.time"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder="">
            </el-date-picker>
        </el-form-item>
        <el-form-item label=":">
           <el-date-picker
              v-model="form2.time2"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder="">
            </el-date-picker>
        </el-form-item>
          <div class="subbtns">
            <el-button type="primary" @click="search"></el-button>
            <el-button @click="reset"></el-button>
          </div>
      </el-form>
</div>
  
      
</template>

<script>
export default {
    data () {
        return {
          region: '',
          user: '',
          value1: '',
          form1: {
            year: '',
            region: '',
            type: '',
            xtype: '',
            name: '',
            time: ''
          },
          form2: {
            year: '',
            region: '',
            type: '',
            xtype: '',
            name: '',
            time: '',
            time2: '',
            jstype: '',
            pytype: '',
            fgtype: ''
          },
          params:{},
          years:[
            {
              value: '',
              label: ''
            },
            {
              value: '2021',
              label: '2021'
            },
            {
              value: '2020',
              label: '2020'
            },
            {
              value: '2019',
              label: '2019'
            },
          ],
           divisions:[
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            }
          ],
           types:[
             {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
          ],
           xztypes:[
             {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
          ],
           fgtypes:[
             {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
          ],
          jstypes:[
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
          ],
          pytypes:[
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            }
          ]
        };
    },
    props: ['sekey'],
    mounted () {
    },
    methods: {
      search(){
        if(this.sekey==1){
          this.params=this.form1
        }else{
          this.params=this.form2
        }
        
        this.$emit("params",{data:this.params})
      },
      reset(){
        
        this.form1.year=''
        this.form1.region=''
         this.form1.type=''
        this.form1.xtype= ''
         this.form1.name= ''
            this.form1.time= ''


          this.form2.year= ''
           this.form2.region= ''
            this.form2.type= ''
            this.form2.xtype= ''
            this.form2.name= ''
            this.form2.time= ''
            this.form2.time2= ''
            this.form2.jstype= ''
            this.form2.pytype= ''
            this.form2.fgtype= ''
      }
    }
};
</script>

<style lang="scss" scoped>
  /deep/.el-form--inline .el-form-item{
    width: 45%;
  }
  /deep/.el-form--inline .el-form-item__content{
    width: 200px;
  }
  .subbtns{
    padding: 10px;
  }
</style>



