// eslint-disable no-indef
import * as czml from "../../../static/js/czml.min";

export default function getCzml(tle, option) {
  // console.warn(tle, option);
  // console.log(tle.tle2.substring(52, 63),'')
  // console.log(parseFloat(tle.tle2.substring(52, 63)),'')
  // const circleTIme = (24.0 / parseFloat(tle.tle2.substring(52, 63))) * 3600 * 1000;
  //
  // let zb = (300 * 1000) / circleTIme;
  // console.log(zb, "")
  // console.log(circleTIme, "");
  const startTime = option.startTime;
  // console.log(startTime, "");
  // console.log(lineStatus);

  const endTIme = new Date(option.startTime.getTime() + 1000 * 60 * 60 * 24);

  // const endTIme = lineStatus ? new Date(startTime.getTime() + circleTIme) : new Date(startTime.getTime() + circleTIme*zb);
  //    --    --
  // option.stepNum = option.stepNum;

  // console.time('--abc')
  const czmlUtil = new czml.Czml(option.text, tle.tle1, tle.tle2, option);
  // console.log("czmlUtil", czmlUtil);
  const czmlValue = czmlUtil.createCzml(startTime, endTIme);
  // const czmlValue = czmlUtil.createLittleCzml(startTime, endTIme,startTime,endTIme2);
  
  // console.timeEnd('--abc')
  // console.log("czmlValue", czmlValue)
  return czmlValue;
}



